$main: #d91a21;
$lightMain: #f51d24;
$mainWhite: #f3f3f3;
$mainDark: #232528;
$mainYellow: #ffa400;

body {
  font-family: 'Oswald', sans-serif;
  background-color: $mainWhite;
  color: $mainDark; }

.text-title {
  font-family: 'Fira Sans', sans-serif;
  font-weight: 500 !important;
  letter-spacing: 0.05rem;
  text-transform: uppercase; }

h1 {
  font-weight: 500; }

.hidden {
  visibility: hidden; }

.text-blue {
  color: $mainDark; }

.text-bright {
  color: $mainWhite; }

.navbar-wrapper {
  background-color: $main !important;
  .nav-link {
    color: $mainWhite !important;
    font-size: 1.3rem;
    text-transform: capitalize; }

  .fa-home {
    font-size: 30px; } }

.search-wrapper {
  margin-left: 5vh;
  h3 {
    display: inline;
    color: $mainWhite;
    font-size: 20px;
    margin-right: 10px; }
  input {
    display: inline;
    border-radius: 30px;
    padding: 2px 11px;
    border: #aba89f 1px solid;
    outline: none;
    width: 25vh; }
  .fa-search {
    margin-right: 10px;
    color: $mainWhite; }
  .search-list {
    color: #fff;
    ul {
      li {
        list-style-type: none;
        border: 2px solid #fff; } } } }

.button-wrapper {
  display: inline-block;
  text-transform: capitalize;
  font-size: 1.4rem;
  background: transparent;
  border: 0.25rem solid $mainWhite;
  color: $mainWhite;
  border-radius: 0.5rem;
  padding: 0.2rem 0.5rem;
  cursor: pointer;
  margin: 0.2rem 0.5rem 0.2rem 0;
  transition: all 0.5s ease-in-out;

  &:hover {
    background: $mainWhite;
    color: $mainDark; }
  &:focus {
    outline: none; }
  &-black {
    border: 0.25rem solid $mainDark;
    color: $mainDark;
    &:hover {
      background: $mainDark;
      color: $mainWhite; } }
  &-main {
    border: 0.25rem solid $lightMain;
    color: $lightMain;
    &:hover {
      background: $lightMain;
      color: $mainWhite; } } }

.card {
  border-color: transparent;
  transition: all 0.4s linear;
  &:hover {
    border: 1px solid rgba(0,0,0, 0.2);
    box-shadow: 2px 2px 5px 0px rgba(0,0,0,0.2); } }

.card-footer {
  background: transparent;
  border-top: transparent;
  transition: all 1s ease;
  &:hover {
    background: rgba(247,247,247, 1); } }

.img-container {
  //height: 240px
  position: relative;
  overflow: hidden;
  display: flex;
  justify-content: center;
  .card-img-top {
    transition: all 1s ease;
    height: 140px;
    width: 107px;
    &-tab {
      height: 140px;
      width: 121px; } }

  &:hover {
    .card-img-top {
      transition: all 1s ease;
      transform: scale(1.15); }
    .cart-btn {
      transform: translate(0,0); } }

  .cart-btn {
    position: absolute;
    bottom: 0;
    right: 0;
    padding: 0.2rem 0.4rem;
    background: $lightMain;
    border: none;
    color: $mainWhite;
    font-size: 1.4rem;
    border-radius: 0.3rem 0 0 0;
    transform: translate(100%, 100%);
    transition: all 1s ease;
    &:hover {
      color: #000;
      cursor: pointer; } } }

.modal-container {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0,0,0,.3);
  display: flex;
  align-items: center;
  justify-content: center;
  .modal-item {
    background-color: $mainWhite;
    padding: 30px;
    border-radius: 7px; } }

.btn-black {
  background: transparent;
  text-transform: uppercase;
  text-align: center;
  font-size: 0.8rem;
  color: $mainDark;
  border: 0.1rem solid $mainDark;
  border-radius: 0px;
  transition: all 0.5s ease; }
.btn-black-hov {
  cursor: pointer;
  &:hover {
    background: $mainDark;
    color: $mainWhite; } }

.cart-icon {
  cursor: pointer;
  font-size: 1.2rem;
  color: $mainYellow; }

.not-found {
  height: 80vh;
  display: flex;
  align-items: center;
  justify-content: center; }

.details-img {
  display: flex;
  justify-content: center;
  height: 244px;
  width: 320px;
  margin-top: 40px;
  margin-bottom: 20px;
  margin-right: 0px; }

.inCart {
  color: $mainWhite;
  &:hover {
    text-decoration: none;
    color: $mainWhite; } }

.pagination {
  transition: 1s ease;
  display: flex;
  flex-direction: row;
  justify-content: center;
  .page-item {
    cursor: pointer;
    margin: 3px;
    .page-link {
      transition: 0.4s ease; } }
  .active {
    transition: 0.4s ease;
    .page-link {
      transition: 0.4s ease;
      background-color: $main;
      border-color: $main;
      &:hover {
        transition: 0.4s ease;
        color: #fff; } } } }

.list-group {
  &-item {
    cursor: pointer; } }

.active {
  background-color: $main !important;
  border-color: $main !important; }

.list-group-item.active {
  z-index: 0; }

.page-item.active .page-link {
  z-index: 0; }

.product {
  margin-top: 0px !important; }

.nav-item {
  margin-left: 6vh; }

@media (max-width: 992px) {
  .product {
    margin-top: 20px !important; } }

@media (max-width: 768px) {
  .search-wrapper {
    margin-left: 2vh; }
  .nav-item {
    margin-left: 0vh; } }
